import React from 'react';
import './App.scss';
import { BrowserRouter as Router, Switch, Route, Redirect } from 'react-router-dom';
import Header from './Components/Header/Header';
import About from './Components/About/About';
import Projects from './Components/Projects/Projects';
import Contact from './Components/Contact/Contact';
import video from './bcblack.mp4';
import ScrollToTop from './Components/ScrollToTop/ScrollToTop';

function App() {
  return (
    <div className="App">
      <video autoPlay="autoplay" loop="loop" muted className="App__background"> 
        <source src={video} type="video/mp4" />
      </video>
      <div className="App__overlay"></div>
      <Router> 
        <ScrollToTop />
        <Header />
        <Switch>
          <Redirect exact from='/portfolio' to='/portfolio/about' />
          <Route exact path='/portfolio/about'>
            <About />
          </Route>
          <Route exact path='/portfolio/projects'>
            <Projects />
          </Route>
          <Route exact path='/portfolio/contact'>
            <Contact />
          </Route>
          <Redirect to="/portfolio" />
        </Switch>
      </Router>
    </div>
  );
}

export default App;

