import React, { useState } from "react";
import axios from "axios";
import Button from "react-bootstrap/Button";

function MyForm() {
  const [serverState, setServerState] = useState({
    submitting: false,
    status: null
  });
  const handleServerResponse = (ok, msg, form) => {
    setServerState({
      submitting: false,
      status: { ok, msg }
    });
    if (ok) {
      form.reset();
    }
  };
  const handleOnSubmit = e => {
    e.preventDefault();
    const form = e.target;
    setServerState({ submitting: true });
    axios({
      method: "post",
      url: "https://formspree.io/mbjzedqy",
      data: new FormData(form)
    })
      .then(r => {
        handleServerResponse(true, "E-mail sent! Thank you!", form);
      })
      .catch(r => {
        handleServerResponse(false, r.response.data.error, form);
      });
  };
  return (
    <div className="contact__form-container">
      <h1 className="contact__me">Contact me</h1>
      <form className="contact__form" onSubmit={handleOnSubmit}>
        <label htmlFor="name">Your Name:</label>
        <input className="contact__inputs" id="name" type="input" name="name" required />
        <label htmlFor="email">Your Email:</label>
        <input className="contact__inputs" id="email" type="email" name="email" required />
        <label htmlFor="message">Message:</label>
        <textarea className="contact__input-message" id="message" name="message" required></textarea>
        <Button className="contact__button" variant="outline-light" type="submit" disabled={serverState.submitting}>
          Submit
        </Button>
        <div className="contact__email-sent">
          {serverState.status && (
            <p className={!serverState.status.ok ? "errorMsg" : ""}>
              {serverState.status.msg}
            </p>
          )}
        </div>
      </form>
    </div>
  );
};

export default MyForm;