      <Navbar collapseOnSelect expand="lg" className="nav-bar__nav" variant="dark">
        <NavLink to='/portfolio/about' className="nav-bar__brand"><h2>Portfolio</h2></NavLink>
        <Navbar.Toggle className="nav-bar__toggle" aria-controls="responsive-navbar-nav"><Hamburger color='#FFF' toggled={isOpen} toggle={setOpen} /></Navbar.Toggle>
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="mr-auto"></Nav>
          <Nav>
            <Nav.Link><NavLink to='/portfolio/about' className="nav-bar__anchor hover" activeClassName="nav-bar__anchor-active">About</NavLink></Nav.Link>
            <Nav.Link><NavLink to='/portfolio/projects' className="nav-bar__anchor hover" activeClassName="nav-bar__anchor-active">Projects</NavLink></Nav.Link>
            <Nav.Link><NavLink to='/portfolio/contact' className="nav-bar__anchor hover" activeClassName="nav-bar__anchor-active">Contact</NavLink></Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
